"use client";

import Image from "next/image";
import React from "react";

type Party = {
  name?: string | null;
  address?: string | null;
  phone?: string | null;
  brn?: string | null;
  vat_no?: string | null;
  customer_code?: string | null;
};

type DocCompany = {
  brn?: string | null;
  vat_no?: string | null;
};

type Totals = {
  subtotal?: number | null;
  vatPercentLabel?: string;
  vat_amount?: number | null;
  total_amount?: number | null;
  previous_balance?: number | null;
  amount_paid?: number | null;
  balance_remaining?: number | null;
  discount_percent?: number | null;
  discount_amount?: number | null;
};

export type RamPotteryDocProps = {
  variant: "INVOICE" | "CREDIT_NOTE";
  docNoLabel: string;
  docNoValue: string;
  dateLabel: string;
  dateValue: string;
  purchaseOrderLabel?: string;
  purchaseOrderValue?: string | null;

  salesRepName?: string | null;
  salesRepPhone?: string | null;

  customer: Party;
  company?: DocCompany;

  tableHeaderRightTitle?: string;

  items: Array<{
    sn: number;
    item_code?: string | null;

    uom?: string | null;
    box_qty?: number | null;
    units_per_box?: number | null;
    total_qty?: number | null;

    description?: string | null;
    unit_price_excl_vat?: number | null;
    unit_vat?: number | null;
    unit_price_incl_vat?: number | null;
    line_total?: number | null;
  }>;

  totals: Totals;

  preparedBy?: string | null;
  deliveredBy?: string | null;

  // NOTE: we intentionally do NOT render any thank-you bar
  footerThanksText?: string;
};

/* =========================================================
   FORMAT RULES
   - 100, 1,000, 10,000, 100,000
   - if decimals exist -> show 2 decimals
   - if integer -> no decimals
========================================================= */
function fmtNumber(n: any, force2 = false) {
  const v = Number(n ?? 0);
  if (!Number.isFinite(v)) return force2 ? "0.00" : "0";
  const hasDecimals = Math.abs(v % 1) > 0.000001;
  const minFrac = force2 ? 2 : hasDecimals ? 2 : 0;
  const maxFrac = 2;
  return new Intl.NumberFormat("en-US", {
    minimumFractionDigits: minFrac,
    maximumFractionDigits: maxFrac,
  }).format(v);
}
function money(n: any) {
  return fmtNumber(n, false);
}
function moneyBlankZero(n: any) {
  const v = Number(n ?? 0);
  if (!Number.isFinite(v) || Math.abs(v) < 0.000001) return "0.00";
  // screenshot shows 2 decimals on totals lines usually; keep consistent here
  return new Intl.NumberFormat("en-US", { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(v);
}
function uomLabel(u: any) {
  const x = String(u || "BOX").toUpperCase();
  return x === "PCS" ? "PCS" : "BOX";
}

export default function RamPotteryDoc(props: RamPotteryDocProps) {
  const {
    variant,
    docNoLabel,
    docNoValue,
    dateLabel,
    dateValue,
    purchaseOrderLabel = "PURCHASE ORDER NO:",
    purchaseOrderValue,
    salesRepName,
    salesRepPhone,
    customer,
    company,
    tableHeaderRightTitle,
    items,
    totals,
    preparedBy,
    deliveredBy,
  } = props;

  const subLine1 = "MANUFACTURER & IMPORTER OF QUALITY CLAY";
  const subLine2 = "PRODUCTS AND OTHER RELIGIOUS ITEMS";

  const addrLine1 = "Robert Kennedy Street, Reunion Maurel,";
  const addrLine2 = "Petit Raffray - Mauritius";

  const vatLabel = totals.vatPercentLabel || "VAT";

  const brn = (company?.brn || "").trim();
  const vatNo = (company?.vat_no || "").trim();

  const repName = (salesRepName || "").trim();
  const repPhone = (salesRepPhone || "").trim();

  const gross = Number(totals.total_amount || 0) + Number(totals.previous_balance || 0);

  const hasDiscount =
    Number(totals.discount_amount || 0) > 0 || Number(totals.discount_percent || 0) > 0;

  const bankLineText = "Bank transfer to 000 44 570 46 59 MCB Bank";

  const docTitle =
    tableHeaderRightTitle || (variant === "INVOICE" ? "VAT INVOICE" : "CREDIT NOTE");

  return (
    <div className="rpdoc-wrap">
      <div className="rpdoc-page">
        {/* HEADER */}
        <div className="rpdoc-headerTop">
          <div className="rpdoc-headerTop-left">
            <Image
              src="/images/logo/logo.png"
              alt="Ram Pottery Logo"
              width={520}
              height={520}
              priority
              className="rpdoc-logoTop"
            />
          </div>

          <div className="rpdoc-headerTop-center">
            <div className="rpdoc-titleOneRow">RAM POTTERY LTD</div>

            <div className="rpdoc-subTwoRows">
              <div>{subLine1}</div>
              <div>{subLine2}</div>
            </div>

            <div className="rpdoc-addrTwoRows">
              <div>{addrLine1}</div>
              <div>{addrLine2}</div>
            </div>

            <div className="rpdoc-phonesOneRow">
              <span className="rpdoc-red">Tel:</span>{" "}
              <span>+230 57788884  +230 58060268  +230 52522844</span>
            </div>

            <div className="rpdoc-mailWebOneRow">
              <span className="rpdoc-red">Email:</span> <span>info@rampottery.com</span>
              <span className="rpdoc-gap" />
              <span className="rpdoc-red">Web:</span> <span>www.rampottery.com</span>
            </div>

            <div className="rpdoc-docOneRow">{docTitle}</div>
          </div>

          {/* spacer to keep center text perfectly centered */}
          <div className="rpdoc-headerTop-right" />
        </div>

        {/* TOP BOXES */}
        <div className="rpdoc-topgrid">
          <div className="rpdoc-box">
            <div className="rpdoc-box-title">CUSTOMER DETAILS</div>
            <div className="rpdoc-box-body">
              <div className="rpdoc-row">
                <span className="k">Name:</span>
                <span className="v">{customer?.name || ""}</span>
              </div>

              <div className="rpdoc-row">
                <span className="k">ADDRESS:</span>
                <span className="v">{customer?.address || ""}</span>
              </div>

              <div className="rpdoc-row">
                <span className="k">Tel:</span>
                <span className="v">{customer?.phone || ""}</span>
              </div>

              <div className="rpdoc-row rpdoc-row-split">
                <span className="rpdoc-pair">
                  <span className="k">BRN:</span>
                  <span className="v">{customer?.brn || ""}</span>
                </span>

                <span className="rpdoc-pair">
                  <span className="k">VAT No:</span>
                  <span className="v">{customer?.vat_no || ""}</span>
                </span>
              </div>

              <div className="rpdoc-row">
                <span className="k">CUSTOMER CODE:</span>
                <span className="v">{customer?.customer_code || ""}</span>
              </div>
            </div>
          </div>

          <div className="rpdoc-box">
            <div className="rpdoc-box-title rpdoc-box-title-center">
              BRN: {brn} {brn || vatNo ? "|" : ""} VAT:{vatNo}
            </div>

            <div className="rpdoc-box-body">
              <div className="rpdoc-row">
                <span className="k">{docNoLabel}</span>
                <span className="v">{docNoValue || "(Auto when saved)"}</span>
              </div>

              <div className="rpdoc-row">
                <span className="k">{dateLabel}</span>
                <span className="v">{dateValue}</span>
              </div>

              <div className="rpdoc-row">
                <span className="k">{purchaseOrderLabel}</span>
                <span className="v">{purchaseOrderValue || ""}</span>
              </div>

              <div className="rpdoc-row">
                <span className="k">SALES REP:</span>
                <span className="v">{repName}</span>
              </div>

              <div className="rpdoc-row">
                <span className="k">Tel:</span>
                <span className="v">{repPhone || ""}</span>
              </div>
            </div>
          </div>
        </div>

        {/* ITEMS TABLE */}
        <table className="rpdoc-table2">
          <colgroup>
            <col style={{ width: "4%" }} />
            <col style={{ width: "10%" }} />
            <col style={{ width: "7%" }} />
            <col style={{ width: "9%" }} />
            <col style={{ width: "9%" }} />
            <col style={{ width: "25%" }} />
            <col style={{ width: "10%" }} />
            <col style={{ width: "6%" }} />
            <col style={{ width: "10%" }} />
            <col style={{ width: "10%" }} />
          </colgroup>

          <thead>
            <tr>
              <th>SN</th>
              <th>ITEM CODE</th>
              <th>BOX</th>
              <th>UNIT<br />PER<br />BOX</th>
              <th>TOTAL<br />QTY</th>
              <th>DESCRIPTION</th>
              <th>UNIT PRICE<br />(EXCL VAT)</th>
              <th>VAT</th>
              <th>UNIT PRICE<br />(INCL VAT)</th>
              <th>TOTAL AMOUNT<br />(INCL VAT)</th>
            </tr>
          </thead>

          <tbody>
            {(items || []).map((it) => {
              const u = uomLabel(it.uom);
              const qty = Number(it.box_qty ?? 0);

              const upb = u === "PCS" ? "" : (it.units_per_box ?? "");
              const tqty = it.total_qty ?? "";

              return (
                <tr key={it.sn}>
                  <td className="c">{it.sn}</td>
                  <td className="c">{it.item_code || ""}</td>
                  <td className="c">{u === "PCS" ? `PCS ${fmtNumber(qty)}` : fmtNumber(qty)}</td>
                  <td className="c">{upb ? fmtNumber(upb) : ""}</td>
                  <td className="c">{tqty ? fmtNumber(tqty) : ""}</td>
                  <td className="l desc">{it.description || ""}</td>
                  <td className="r">{new Intl.NumberFormat("en-US", { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(Number(it.unit_price_excl_vat || 0))}</td>
                  <td className="r">{new Intl.NumberFormat("en-US", { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(Number(it.unit_vat || 0))}</td>
                  <td className="r">{new Intl.NumberFormat("en-US", { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(Number(it.unit_price_incl_vat || 0))}</td>
                  <td className="r">{new Intl.NumberFormat("en-US", { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(Number(it.line_total || 0))}</td>
                </tr>
              );
            })}
          </tbody>
        </table>

        {/* BOTTOM GRID: NOTES + TOTALS */}
        <div className="rpdoc-bottomgrid">
          <div className="rpdoc-notes">
            <div className="rpdoc-notes-title">Note:</div>
            <ul>
              <li><b>Goods once sold cannot be returned or exchanged.</b></li>
              <li>For any other manufacturing defects, must provide this invoice for a refund or exchange.</li>
              <li>Customer must verify that the quantity of goods conforms with their invoice; otherwise, we will not be responsible after delivery.</li>
              <li>Interest of 1% above the bank rate will be charged on sum due if not settled within 30 days.</li>
              <li>
                All cheques to be issued on{" "}
                <b className="redStrong">RAM POTTERY LTD</b>.
              </li>
              <li className="redStrong">{bankLineText}</li>
            </ul>
          </div>

          <div className="rpdoc-totals">
            <div className="trow"><span>SUB TOTAL</span><span className="money">{moneyBlankZero(totals.subtotal)}</span></div>
            <div className="trow"><span>{vatLabel}</span><span className="money">{moneyBlankZero(totals.vat_amount)}</span></div>

            {hasDiscount ? (
              <div className="trow">
                <span>
                  DISCOUNT
                  {Number(totals.discount_percent || 0) > 0 ? ` (${Number(totals.discount_percent || 0).toFixed(0)}%)` : ""}
                </span>
                <span className="money">- {moneyBlankZero(totals.discount_amount)}</span>
              </div>
            ) : null}

            <div className="trow strong"><span>TOTAL AMOUNT</span><span className="money">{moneyBlankZero(totals.total_amount)}</span></div>
            <div className="trow"><span>PREVIOUS BALANCE</span><span className="money">{moneyBlankZero(totals.previous_balance)}</span></div>
            <div className="trow strong"><span>GROSS TOTAL</span><span className="money">{new Intl.NumberFormat("en-US",{minimumFractionDigits:2,maximumFractionDigits:2}).format(gross)}</span></div>
            <div className="trow"><span>AMOUNT PAID</span><span className="money">{moneyBlankZero(totals.amount_paid)}</span></div>
            <div className="trow strong"><span>BALANCE REMAINING</span><span className="money">{moneyBlankZero(totals.balance_remaining)}</span></div>
          </div>
        </div>

        {/* SIGNATURES */}
        <div className="rpdoc-sign">
          <div className="signcol">
            <div className="line" />
            <div className="label">Signature</div>
            <div className="meta">Prepared by: {preparedBy || ""}</div>
          </div>

          <div className="signcol">
            <div className="line" />
            <div className="label">Signature</div>
            <div className="meta">Delivered by: {deliveredBy || ""}</div>
          </div>

          <div className="signcol">
            <div className="line" />
            <div className="label">Customer Signature</div>
            <div className="meta">Customer Name: __________________</div>
            <div className="meta"><i>Please verify before sign</i></div>
          </div>
        </div>
      </div>

      {/* FULL CSS (PRINT + SCREEN) */}
      <style jsx global>{`
        :root{
          --rp-red:#b10000;
          --rp-redbar:#d50000;
          --rp-black:#111;
        }

        .rpdoc-wrap{
          width:100%;
          display:flex;
          justify-content:center;
          background:#fff;
        }

        /* A4 page like screenshot */
        .rpdoc-page{
          width: 200mm;
          min-height: 285mm;
          background:#fff;
          color: var(--rp-black);
          border: 2px solid var(--rp-black);
          padding: 10mm 10mm 8mm;
          box-sizing:border-box;
          font-family: Arial, Helvetica, sans-serif;
        }

        /* ===== HEADER: LOGO LEFT / TEXT CENTER ===== */
        .rpdoc-headerTop{
          display:grid;
          grid-template-columns: 62mm 1fr 62mm;
          column-gap: 6mm;
          align-items:start;
          margin-bottom: 10px;
        }

        .rpdoc-headerTop-left{
          display:flex;
          align-items:flex-start;
          justify-content:flex-start;
          padding-top: 2mm;
        }

        .rpdoc-logoTop{
          width: 58mm;
          height: auto;
          display:block;
          object-fit: contain;
        }

        .rpdoc-headerTop-center{
          text-align:center;
          padding-top: 1mm;
        }

        .rpdoc-headerTop-right{ width: 62mm; }

        .rpdoc-titleOneRow{
          font-family: "Times New Roman", Times, serif;
          font-weight: 900;
          color: var(--rp-red);
          font-size: 34px;
          letter-spacing: .8px;
          line-height: 1;
          white-space: nowrap;
          margin: 0;
        }

        .rpdoc-subTwoRows{
          margin-top: 4px;
          font-family: "Times New Roman", Times, serif;
          font-style: italic;
          font-weight: 700;
          color:#222;
          font-size: 10.5px;
          line-height: 1.25;
        }
        .rpdoc-subTwoRows > div{ white-space: nowrap; }

        .rpdoc-addrTwoRows{
          margin-top: 6px;
          font-weight: 900;
          color:#111;
          font-size: 11.5px;
          line-height: 1.25;
        }
        .rpdoc-addrTwoRows > div{ white-space: nowrap; }

        .rpdoc-phonesOneRow,
        .rpdoc-mailWebOneRow{
          margin-top: 6px;
          font-weight: 900;
          color:#111;
          font-size: 11.5px;
          white-space: nowrap;
        }

        .rpdoc-red{ color: var(--rp-red); font-weight: 900; }
        .rpdoc-gap{ display:inline-block; width: 16px; }

        .rpdoc-docOneRow{
          margin-top: 8px;
          font-family: "Times New Roman", Times, serif;
          font-weight: 900;
          color: var(--rp-red);
          font-size: 14px;
          letter-spacing: .6px;
          white-space: nowrap;
        }

        /* ===== TOP BOXES ===== */
        .rpdoc-topgrid{
          margin-top: 12px;
          display:grid;
          grid-template-columns: 1fr 1fr;
          gap: 12px;
        }

        .rpdoc-box{
          border: 2px solid var(--rp-black);
          min-height: 54mm;
        }

        .rpdoc-box-title{
          background: var(--rp-redbar);
          color:#fff;
          font-weight: 900;
          padding: 8px 10px;
          text-align:center;
          text-transform: uppercase;
          letter-spacing: .3px;
          font-size: 13px;
        }

        .rpdoc-box-body{
          padding: 12px 12px 10px;
          font-size: 11px;
        }

        .rpdoc-row{
          display:grid;
          grid-template-columns: 42mm 1fr;
          gap: 6px;
          align-items: baseline;
          padding: 4px 0;
        }
        .rpdoc-row .k{
          font-weight: 900;
          color: var(--rp-redbar);
          white-space: nowrap;
        }
        .rpdoc-row .v{
          font-weight: 900;
          color:#111;
          min-width: 0;
          overflow:hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }

        .rpdoc-row-split{
          display:flex;
          justify-content:flex-start;
          gap:18px;
          flex-wrap:wrap;
          padding: 4px 0;
        }
        .rpdoc-pair{
          display:inline-flex;
          gap:6px;
          align-items:baseline;
        }

        /* ===== TABLE ===== */
        .rpdoc-table2{
          width:100%;
          border-collapse: collapse;
          margin-top: 12px;
          border:2px solid var(--rp-black);
          table-layout: fixed;
          font-size: 10.5px;
        }

        .rpdoc-table2 th{
          background: var(--rp-redbar);
          color:#fff;
          font-weight: 900;
          text-transform: uppercase;
          letter-spacing:.2px;
          border:2px solid var(--rp-black);
          padding: 8px 6px;
          text-align:center;
          line-height:1.05;
        }

        .rpdoc-table2 td{
          border:2px solid var(--rp-black);
          padding: 10px 6px;
          font-weight: 700;
          vertical-align: top;
          line-height: 1.15;
        }

        .rpdoc-table2 td.c{ text-align:center; }
        .rpdoc-table2 td.r{ text-align:right; }
        .rpdoc-table2 td.l{ text-align:left; }

        .rpdoc-table2 td.desc{
          font-weight: 700;
          word-break: break-word;
        }

        /* ===== BOTTOM GRID ===== */
        .rpdoc-bottomgrid{
          margin-top: 10px;
          display:grid;
          grid-template-columns: 1.35fr .65fr;
          gap: 12px;
          align-items: start;
        }

        .rpdoc-notes{
          border:2px solid var(--rp-black);
          padding: 10px 12px;
          font-size: 10.5px;
          min-height: 48mm;
        }

        .rpdoc-notes-title{
          font-weight: 900;
          margin-bottom: 6px;
        }

        .rpdoc-notes ul{
          margin: 0;
          padding-left: 18px;
        }
        .rpdoc-notes li{
          margin: 6px 0;
          line-height: 1.2;
        }

        .redStrong{
          color: var(--rp-red);
          font-weight: 900;
        }

        .rpdoc-totals{
          border:2px solid var(--rp-black);
          font-size: 11px;
        }

        .rpdoc-totals .trow{
          display:flex;
          justify-content:space-between;
          gap:10px;
          padding: 9px 10px;
          border-bottom:2px solid var(--rp-black);
          font-weight: 900;
        }

        .rpdoc-totals .trow:last-child{
          border-bottom: none;
        }

        .rpdoc-totals .trow.strong{
          font-weight: 900;
        }

        .rpdoc-totals .money{
          min-width: 42mm;
          text-align:right;
        }

        /* ===== SIGNATURES ===== */
        .rpdoc-sign{
          margin-top: 14px;
          border-top: 2px solid var(--rp-black);
          padding-top: 10px;
          display:grid;
          grid-template-columns: 1fr 1fr 1fr;
          gap: 18px;
        }

        .rpdoc-sign .signcol{
          font-size: 10.5px;
          font-weight: 700;
        }

        .rpdoc-sign .line{
          border-bottom: 2px solid var(--rp-black);
          height: 14px;
          margin-bottom: 6px;
        }

        .rpdoc-sign .label{
          font-weight: 900;
          margin-bottom: 2px;
        }

        .rpdoc-sign .meta{
          margin-top: 2px;
        }

        /* ===== PRINT ===== */
        @media print{
          @page{ size:A4 portrait; margin:6mm; }
          *{ -webkit-print-color-adjust: exact !important; print-color-adjust: exact !important; }
          thead{ display: table-header-group !important; }
          .rpdoc-wrap{ background:#fff !important; }
        }
      `}</style>
    </div>
  );
}

